import React, { Component } from 'react';
import {Col,Row,Form, Icon, Input, Button, Menu ,Modal,Avatar,message,Tooltip} from 'antd';
import {getService} from '../model/fetch';
import {Content} from './newMenu';
import {BrowserRouter as Router,Route,Link} from 'react-router-dom';
import './new-header.css';
import Register from './register';
const FormItem = Form.Item;
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;
const Search = Input.Search;


class LoginForm extends React.Component {
  constructor(props){
    super(props);
    this.state={
      loading:false,
      visible: false,
      registerLoading:false,
    };
  }
  showModal = () => {
    this.setState({ visible: true });
  }
  handleCancel = () => {
    this.setState({ visible: false });
  }
  handleCreate = () => {
    const form = this.form;
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      if(values.account && values.password && values.name){
        this.setState({
          registerLoading:true,
        });
      }
      getService('http://www.henryzhang.com.cn:8080/register/'+values.account+'&'+values.password+'&'+values.name,(data)=>{
        if(data.success){
          if(data.data==="False"){
            message.error("",3);
            this.setState({
              registerLoading:false
            });
          }else{
            message.success("",3);
            this.setState({
              registerLoading:false,
              visible: false
            });
          }           
        }
      })
    });
  }
  saveFormRef = (form) => {
    this.form = form;
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({
          loading:true
        })
        getService('http://www.henryzhang.com.cn:8080/login/'+values.userName+'&'+values.password,(data)=>{
          if(data.success){
            if(data.data==="False"){
              message.error("",3);
              this.setState({
                loading:false
              })
            }else{
              sessionStorage.setItem("name", data.data[0].name);
              sessionStorage.setItem("uid", data.data[0].uid);
              this.props.changeLogin();
              message.success("",3);
              this.setState({
                loading:false
              })
            }           
          }
        })
      }
    });
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form" layout="vertical" hideRequiredMark > 
        <FormItem label=""> 
          {getFieldDecorator('userName', {
            rules: [{ required: true, message: '' }],
          })(
            <Input prefix={<Icon type="user" style={{ fontSize: 13 }} />} placeholder="" />
          )}
        </FormItem>
        <FormItem label="">
          {getFieldDecorator('password', {
            rules: [{ required: true, message: '' }],
          })(
            <Input prefix={<Icon type="lock" style={{ fontSize: 13 }} />} type="password" placeholder="" />
          )}
        </FormItem>
        <FormItem>
          <Button type="primary" htmlType="submit" className="login-form-button">
            
          </Button>
        </FormItem>
        <FormItem>
          <a onClick={this.showModal}></a>
        </FormItem>
        <Modal visible={this.state.visible} title="" okText="" cancelText="" onCancel={this.handleCancel} onOk={this.handleCreate} confirmLoading={this.state.registerLoading}>
          <Register  ref={this.saveFormRef}/>
        </Modal>  
      </Form>
    );
  }
}

const Login = Form.create()(LoginForm);

class Nav extends Component {
    render() {
      return (
        <Menu mode="horizontal">
          <Menu.Item key="homepage">
            <Link to="/">
              <Icon type="home" />
            </Link>
          </Menu.Item>
          <Menu.Item key="menus">
            <Link to="/menus">
              <Icon type="appstore-o" />
            </Link>
          </Menu.Item>
          {/* <SubMenu title={<span><Icon type="setting" />Navigation Three - Submenu</span>}>
            <MenuItemGroup title="Item 1">
              <Menu.Item key="setting:1">Option 1</Menu.Item>
              <Menu.Item key="setting:2">Option 2</Menu.Item>
            </MenuItemGroup>
            <MenuItemGroup title="Item 2">
              <Menu.Item key="setting:3">Option 3</Menu.Item>
              <Menu.Item key="setting:4">Option 4</Menu.Item>
            </MenuItemGroup>
          </SubMenu> */}
          <div className="newHeader-user" style={this.props.isLogin?{}:{display:'none'}}>
            <Icon type="user" />
            {sessionStorage.getItem('name')}
            <Tooltip title="">
              <Icon type="poweroff" onClick={this.props.loginOut}/>
            </Tooltip>            
          </div>
        </Menu>       
      );
    }
  }


class Header extends Component {

  render() {
    const {changeLogin,isLogin}=this.props;
    return (
      <header className="header">
        <div className="overlay"></div>
        <Row gutter={40} className="header-main">
          <Col span={12}>
            <h1 className="header-h1"></h1>
          </Col>
          <Col span={6} offset={4}>
          {
            isLogin?<div />:<div className="login-box">
                              <h2 className="form-h2"></h2>
                              <Login changeLogin={changeLogin}/>
                            </div>
          }            
          </Col>
        </Row>
      </header>        
    );
  }
}

export {Header,Nav};



